<template>
  <div class="container">

    <div class="main-background-desk" v-if="!browserInfo.mobile">
      <router-link to="/">
        <img class="back-button" src="../../assets/back_button.png"/>
      </router-link>

      <img class="sign-in-title" src="../../assets/sign_in_title.png"/>

      <div class="username-section">
        <input class="username-input" v-model="username" type="text" placeholder=""/>
      </div>

      <div class="password-section">
        <input class="password-input" v-model="password" type="password" placeholder=""/>
      </div>

      <img class="sign-in-button" v-on:click="signin" src="../../assets/sign_in_button.png"/>

      <router-link to="/signup/">
        <img class="sign-up-button" src="../../assets/register_hint_icon.png"/>
      </router-link>

    </div>
  </div>
</template>

<script>
var Account = require("nebulas").Account;
  export default {
    name: "index",
    data: function () {
      return {
        browserInfo: {mobile: false},
        username: "",
        password: ""
      }
    },
    methods: {
      $ready(fn) {
        if (process.env.NODE_ENV === 'production') {
          return this.$nextTick(fn);
        }
        setTimeout(() => {
          this.$nextTick(fn);
        });
      },
      signin: function() {
        this.$http.post('http://0.0.0.0:8000/api/user/login', {
          username: this.username,
          password: this.password
        }).then(function(response){
          this.$store.commit('setPrivateKey', response.data.private_key)
          this.$router.go(-1)
        });
      },
      getBrowserInfo() {
        var u = navigator.userAgent, app = navigator.appVersion;
        this.browserInfo = {//
          trident: u.indexOf('Trident') > -1, //IE
          presto: u.indexOf('Presto') > -1, //opera
          webKit: u.indexOf('AppleWebKit') > -1, //
          gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
          mobile: !!u.match(/AppleWebKit.*Mobile.*/) || u.indexOf('iPad') > -1, //
          ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
          android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
          iPhone: u.indexOf('iPhone') > -1, //iPhoneQQHD
          iPad: u.indexOf('iPad') > -1, //iPad
          webApp: u.indexOf('Safari') == -1 //web
        };
        console.log(this.browserInfo);
        console.log("!!!!!!!!!!!!!!!!!!!!!!");
      },
    },
    mounted() {
      this.$nextTick(function () {
        this.getBrowserInfo();
        this.getDeviceRatio();
      });
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "./index.scss";
</style>



